import { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../../hooks/useAuth';
import { supabase } from '../../lib/supabase';

interface Store {
  id: string;
  nombre: string;
  codigo: string;
}

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [selectedStore, setSelectedStore] = useState('');
  const [stores, setStores] = useState<Store[]>([]);
  const [loadingStores, setLoadingStores] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const { signIn, isAuthenticated, loading: authLoading } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();

  const from = location.state?.from?.pathname || '/dashboard';

  // Redirigir si ya est√° autenticado
  useEffect(() => {
    if (isAuthenticated && !authLoading) {
      navigate(from, { replace: true });
    }
  }, [isAuthenticated, authLoading, navigate, from]);

  // ‚úÖ NUEVA FUNCI√ìN: Cargar tiendas del usuario DESPU√âS de autenticarse
  const loadUserStores = async (userId: string) => {
    try {
      setLoadingStores(true);
      console.log('üè™ [LoginPage] Cargando tiendas del usuario:', userId);
      
      // Obtener solo las tiendas asignadas al usuario
      const { data, error } = await supabase
        .from('usuario_tiendas')
        .select('tienda_id, tiendas(id, nombre, codigo, activo)')
        .eq('usuario_id', userId)
        .eq('activo', true);

      if (error) {
        console.error('‚ùå [LoginPage] Error cargando tiendas del usuario:', error);
        setError('Error al cargar tus tiendas. Intenta nuevamente.');
        setStores([]);
        return;
      }

      // Filtrar tiendas activas
      const userStores = (data || [])
        .filter(ut => ut.tiendas && ut.tiendas.activo)
        .map(ut => ({
          id: ut.tiendas.id,
          nombre: ut.tiendas.nombre,
          codigo: ut.tiendas.codigo
        }));

      console.log('‚úÖ [LoginPage] Tiendas del usuario cargadas:', userStores.length);
      setStores(userStores);
      
      // Si solo hay una tienda, seleccionarla autom√°ticamente
      if (userStores.length === 1) {
        setSelectedStore(userStores[0].id);
        console.log('‚úÖ [LoginPage] Auto-seleccionada √∫nica tienda:', userStores[0].nombre);
      }
    } catch (err) {
      console.error('‚ùå [LoginPage] Error en loadUserStores:', err);
      setError('Error al cargar tus tiendas. Intenta nuevamente.');
      setStores([]);
    } finally {
      setLoadingStores(false);
    }
  };

  // ‚úÖ NUEVO FLUJO: Autenticaci√≥n en dos pasos
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // PASO 1: Si no hay tiendas cargadas, primero autenticar para obtenerlas
    if (stores.length === 0) {
      setLoading(true);
      
      try {
        console.log('üîê [LoginPage] PASO 1: Autenticando para obtener tiendas...');
        
        // Autenticar SIN tienda (solo para obtener el userId)
        const { data, error } = await supabase.auth.signInWithPassword({
          email,
          password,
        });
        
        if (error) {
          console.error('‚ùå [LoginPage] Error de autenticaci√≥n:', error);
          
          // Manejo mejorado de errores
          if (error.message?.includes('Invalid login credentials')) {
            setError('Email o contrase√±a incorrectos');
          } else if (error.message?.includes('Email not confirmed')) {
            setError('Tu cuenta a√∫n no ha sido confirmada. Por favor revisa tu correo electr√≥nico y haz clic en el enlace de confirmaci√≥n. Si no recibiste el correo, contacta al administrador.');
          } else if (error.message?.includes('Email link is invalid or has expired')) {
            setError('El enlace de confirmaci√≥n ha expirado. Por favor solicita un nuevo enlace de confirmaci√≥n.');
          } else if (error.message?.includes('User not found')) {
            setError('No existe una cuenta con este correo electr√≥nico');
          } else {
            setError(`Error al iniciar sesi√≥n: ${error.message || 'Intenta nuevamente'}`);
          }
          
          setLoading(false);
          return;
        }
        
        if (!data.session?.user) {
          setError('Error al iniciar sesi√≥n. Intenta nuevamente.');
          setLoading(false);
          return;
        }
        
        console.log('‚úÖ [LoginPage] Autenticaci√≥n exitosa, cargando tiendas...');
        
        // Cargar tiendas del usuario
        await loadUserStores(data.session.user.id);
        
        setLoading(false);
        
        // Mostrar mensaje para seleccionar tienda
        if (stores.length === 0) {
          setError('No tienes tiendas asignadas. Contacta al administrador.');
          // Cerrar sesi√≥n porque no puede continuar
          await supabase.auth.signOut();
        }
        
        return;
      } catch (err: any) {
        console.error('‚ùå [LoginPage] Error inesperado en PASO 1:', err);
        setError(`Error inesperado: ${err?.message || 'Intenta nuevamente'}`);
        setLoading(false);
        return;
      }
    }

    // PASO 2: Ya hay tiendas cargadas, validar selecci√≥n y completar login
    if (!selectedStore) {
      setError('Por favor selecciona una tienda');
      return;
    }

    setLoading(true);

    try {
      console.log('üîê [LoginPage] PASO 2: Completando login con tienda seleccionada...');
      
      const { error } = await signIn(email, password, selectedStore);
      
      if (error) {
        console.error('‚ùå [LoginPage] Error de login:', error);
        setError(`Error al iniciar sesi√≥n: ${error.message || 'Intenta nuevamente'}`);
        setLoading(false);
        
        // Si el error es de acceso a tienda, limpiar y volver a PASO 1
        if (error.message?.includes('No tienes acceso')) {
          setStores([]);
          setSelectedStore('');
        }
      } else {
        console.log('‚úÖ [LoginPage] Login exitoso, redirigiendo a:', from);
      }
    } catch (err: any) {
      console.error('‚ùå [LoginPage] Error inesperado en PASO 2:', err);
      setError(`Error inesperado: ${err?.message || 'Intenta nuevamente'}`);
      setLoading(false);
    }
  };

  // Mostrar loading si est√° autenticando
  if (authLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center p-4">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">Verificando sesi√≥n...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center p-4">
      <div className="max-w-md w-full space-y-8">
        <div className="text-center">
          <div className="mx-auto h-20 w-20 mb-4 flex items-center justify-center">
            <img 
              src="https://static.readdy.ai/image/96746b7ba583c55b81aa58d37fd022fd/8044848901197a0577b8befd634c6da9.png" 
              alt="Logo del Sistema" 
              className="h-16 w-auto object-contain"
            />
          </div>
          <h2 className="text-3xl font-bold text-gray-900 mb-2">
            SCO
          </h2>
          <p className="text-gray-600">
            Ingresa a tu cuenta para continuar
          </p>
        </div>

        <div className="bg-white rounded-lg shadow-lg p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
                Correo electr√≥nico
              </label>
              <input
                id="email"
                name="email"
                type="email"
                autoComplete="email"
                required
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                disabled={stores.length > 0}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 disabled:bg-gray-100 disabled:cursor-not-allowed"
                placeholder="tu@email.com"
              />
            </div>

            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
                Contrase√±a
              </label>
              <input
                id="password"
                name="password"
                type="password"
                autoComplete="current-password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                disabled={stores.length > 0}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 disabled:bg-gray-100 disabled:cursor-not-allowed"
                placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
              />
            </div>

            {/* Mostrar selector de tienda solo despu√©s de autenticar */}
            {stores.length > 0 && (
              <div>
                <label htmlFor="store" className="block text-sm font-medium text-gray-700 mb-2">
                  Selecciona tu tienda
                </label>
                {loadingStores ? (
                  <div className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-500">
                    Cargando tus tiendas...
                  </div>
                ) : (
                  <select
                    id="store"
                    name="store"
                    required
                    value={selectedStore}
                    onChange={(e) => setSelectedStore(e.target.value)}
                    className="w-full px-3 py-2 pr-8 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                  >
                    <option value="">Selecciona una tienda</option>
                    {stores.map((store) => (
                      <option key={store.id} value={store.id}>
                        {store.nombre} ({store.codigo})
                      </option>
                    ))}
                  </select>
                )}
                
                {/* Bot√≥n para cambiar de usuario */}
                <button
                  type="button"
                  onClick={async () => {
                    await supabase.auth.signOut();
                    setStores([]);
                    setSelectedStore('');
                    setEmail('');
                    setPassword('');
                    setError('');
                  }}
                  className="mt-2 text-sm text-indigo-600 hover:text-indigo-500"
                >
                  ‚Üê Usar otra cuenta
                </button>
              </div>
            )}

            {error && (
              <div className="bg-red-50 border border-red-200 rounded-md p-3">
                <p className="text-sm text-red-600">{error}</p>
              </div>
            )}

            {stores.length === 0 && (
              <div className="flex items-center justify-between">
                <Link
                  to="/auth/forgot-password"
                  className="text-sm text-indigo-600 hover:text-indigo-500"
                >
                  ¬øOlvidaste tu contrase√±a?
                </Link>
              </div>
            )}

            <button
              type="submit"
              disabled={loading || loadingStores}
              className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:opacity-50 disabled:cursor-not-allowed whitespace-nowrap"
            >
              {loading ? (
                <div className="flex items-center">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  {stores.length === 0 ? 'Verificando...' : 'Iniciando sesi√≥n...'}
                </div>
              ) : (
                stores.length === 0 ? 'Continuar' : 'Iniciar sesi√≥n'
              )}
            </button>
          </form>

          <div className="mt-6 text-center">
            <p className="text-sm text-gray-600">
              ¬øNo tienes una cuenta?{' '}
              <Link
                to="/auth/register"
                className="font-medium text-indigo-600 hover:text-indigo-500"
              >
                Reg√≠strate aqu√≠
              </Link>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
